<template>
  <div>
    <Header name="支付"></Header>
    <div class="paymentHead">
      <div class="content01">
        <img :src="require('@/assets/image/yfje.png')" alt="" />
        <span>应付金额</span>
      </div>
      <!-- (order.payAmount - commitAmount).toFixed(2) -->
      <div class="content02">￥<span>{{(freightAmountFlag ? order.freightAmount : (order.payAmount - commitAmount)).toFixed(2)}}</span></div>
      <div class="content03">支付剩余时间&nbsp;&nbsp;{{ hh }}时{{ mm }}分{{ ss }}秒</div>
    </div>
    <div v-if="showTip" id="weixinTip" ref="weixin" class="wxtip">
      <p class="wxtip-txt">
        在微信中无法跳转支付宝
        <br />
        <span class="qx" style="margin-right: 5px;" v-clipboard="copyUrl" @success="handleCopy" @error="handleCopyFail">复制</span>路径或
        <span class="qx" @click="goInfo">取消</span>
      </p>
    </div>
    <div class="paymentTitle">支付方式</div>
    <div class="paymentMode">
      <div class="dgfk" @click="chickValue('1')">
        <div class="left">
          <img :src="require('@/assets/image/dgfk.png')" alt=""/>
          <span>对公账户支付</span>
        </div>
        <div class="right">
          <img :src="value === '1' ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" alt=""/>
        </div>
      </div>
      <div class="wechat" @click="chickValue('2')" v-if="isShow">
        <div class="left">
          <img :src="require('@/assets/image/wechat.png')" alt="" />
          <span>微信支付</span>
        </div>
        <div class="right">
          <img :src="value === '2' ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" alt=""/>
        </div>
      </div>
      <div class="alipay" @click="chickValue('3')" v-if="isShow">
        <div class="left">
          <img :src="require('@/assets/image/alipay.png')" alt="" />
          <span>支付宝支付</span>
        </div>
        <div class="right">
          <img :src="value === '3' ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" alt=""/>
        </div>
      </div>
    </div>
    <van-button class="paymentBtn" @click="goUserCompany(freightAmountFlag ? order.freightAmount : (order.payAmount - commitAmount))" type="primary" :loading="load" block loading-text="支付中...">立即支付</van-button>
    <form method="post" :action="h5Url">
      <input type="submit" value="" style="display:none" >
    </form>
  </div>
</template>

<script>
  
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'

export default {
  name: "payment",
  components :{
    Header
  },
  data() {
    return {
      isShow: false,
      commitAmount: 0,
      value: '1',
      load: false,
      showTip: false,
      copyUrl: '',
      h5Url: '',
      params: {
        cartIds: [],
        memberReceiveAddressId: '',
        orderSn: '',
        payType: ''
      },
      order: '',
      orderItemList: '',
      hh: '00',
      mm: '00',
      ss: '00',
      createTm: '',
      flagTime: false,
      isWeixin: false,
      token: '',
      WsUrl: process.env.WsUrl,
      HttpUrl: process.env.HttpUrl,
      socket: "",
      freightAmountFlag: false
    }
  },
  mounted () {
    // if (window.history && window.history.pushState) {
    //   // 向历史记录中插入了当前页
    //   history.pushState(null, null, document.URL);
    //   window.addEventListener('popstate', this.goBack, false);
    // }
    this.getData()
  },
  methods: {
    goBack () {
      // sessionStorage.clear();
      // window.history.back();
      if (localStorage.getItem('historyUrl') === '/user/orderdetails') {
        this.$router.push({
          path: '/user/orderdetails',
          query: {
            id: this.order.id
          }
        })
      } else {
        this.$router.push({
          path:'/user/myorders',
          query: {
            'toorders': -1
          }
        })
      }
    },
    getData () {
      let me = this
      me.orderItemList = JSON.parse(me.$route.query.orderItemList)
      me.order = JSON.parse(me.$route.query.order)
      me.token = me.$route.query.token
      me.params.cartIds = localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(',')
      me.params.memberReceiveAddressId = localStorage.getItem('addRessDataId')
      me.params.orderSn = me.order.orderSn
      me.handleChange(me.order.createTime)
      let payVoucherFlag = true
      if(me.order.payVoucherList){
        if(me.order.payVoucherList.length === 0){
          me.isShow = true
        }else{
          me.isShow = false
        }
      }else{
        me.isShow = true
      }
      me.order.payVoucherList && me.order.payVoucherList.forEach(item => {
        // if (item.isFreight !== '1') {
          if(item.confirm === 1) {
            me.commitAmount += item.commitAmount
          }
        // }
        if (item.confirm !== 2) {
          payVoucherFlag = false
        }
      });
      // if (payVoucherFlag) {
      //   if ((me.order.useCredit || me.order.voucherAmount) && me.order.freightAmount) {
      //     me.freightAmountFlag = true
      //     me.commitAmount = me.order.freightAmount
      //   }
      // }
    },
    handleChange (tm) {
      let me = this
      me.createTm = tm
      let time = setInterval(() => {
        if (me.flagTime == true) {
          clearInterval(time)
        }
        me.timeDown()
      }, 500)
    },
    timeDown () {
      const endTime = new Date(this.createTm).getTime() + 24 * 60 * 60 * 1000;
      const nowTime = new Date(this.createTm);
      var date = new Date();
      var now = date.getTime(nowTime);
      var endDate = new Date(endTime);
      var end = endDate.getTime();
      var rightTime = end - now;
      if (rightTime > 0) {
        var hh = Math.floor((rightTime / 1000 / 60 / 60) % 24);
        var mm = Math.floor((rightTime / 1000 / 60) % 60);
        var ss = Math.floor((rightTime / 1000) % 60);
        this.hh = hh;
        this.mm = mm;
        this.ss = ss;
      }
    },
    chickValue (sty) {
      this.value = sty
    },
    goUserCompany (data) {
      data = data.toFixed(2)
      let me = this
      if (!localStorage.getItem('token')) {
        localStorage.setItem('token', me.token)
      }
      const { userAgent } = navigator
      me.isWeixin = !!/MicroMessenger/i.test(userAgent)
      // me.isAndroid = userAgent.indexOf('Android') > -1 || userAgent.indexOf('Adr') > -1
      // me.isMac = !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      if (me.value === '1') {
        me.$router.push({
          path: '/user/company',
          query: {
            'orderInfo': JSON.stringify(me.params),
            'payAmount': data,
            'freightAmountFlag': me.freightAmountFlag
          }
        })
      } else if (me.value === '2') {
        me.load = true
        api.orderPayOrder({
          orderSn: me.params.orderSn,
          payType: 2,
          mobile: true,
          openId: localStorage.getItem('openId') && localStorage.getItem('openId') !== 'undefined' ? localStorage.getItem('openId') : '',
          weixin: me.isWeixin
        }).then(res => {
          if (res) {
            if (me.isWeixin) {
              me.onBridgeReady(res.data)
            } else {
              me.h5Url = res.data.h5Url + '&redirect_url=' + me.HttpUrl + 'user/myorders/index'
              me.$nextTick(()=>{
                document.forms[0].submit()
              })
              
              if (me.socket && me.socket.onclose) {
                me.socket.onclose = me.close()
              }
              // 初始化 'wss://mall.hengleikj.com:8085/ws/'
              me.path = me.WsUrl + me.params.orderSn + '?Authorization=Bearer ' + localStorage.getItem('token')
              console.log("URL======>" + me.path)
              me.init()
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally((res)=>{
          me.load = false
        })
      } else if (me.value === '3') {
        if (me.isWeixin) {
          me.showTip = true
        } else {
          me.handlePay()
        }
      }
    },
    onBridgeReady (info) {
      let me = this
      WeixinJSBridge.invoke( 'getBrandWCPayRequest', {
        "appId": info.appId,    
        "timeStamp": info.timeStamp + '',
        "nonceStr": info.nonceStr,    
        "package": info.package,
        "signType": info.signType,
        "paySign": info.paySign 
      },function (res) {
        console.log('微信支付回调:', res)
        if (res.err_msg == "get_brand_wcpay_request:ok") {
          console.log("------>成功")
          me.$notify({ type: 'success', message: '成功' })
          me.$router.push({
            path:'/user/myorders',
            query: {
              'toorders':-1,
            }
          })
        } else {
          console.log("------>失败")
          me.$notify({ type: 'success', message: '失败' })
        }
      })
      // if (typeof WeixinJSBridge == "undefined") {
      //   if (document.addEventListener) {
      //     document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
      //   } else if (document.attachEvent) {
      //     document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
      //     document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
      //   }
      // } else {
      //   onBridgeReady();
      // }
    },
    goInfo () {
      this.showTip = false
    },
    handleCopy () {
      this.$toast("复制成功，请前往浏览器");
    },
    handleCopyFail () {
      this.$toast("复制失败");
    },
    // 支付宝
    handlePay () {
      let me = this
      me.load = true
      api.orderPayOrder({
        orderSn: me.params.orderSn,
        payType: 1,
        mobile: true
      }).then(res => {
        if (res) {
          // res.split("</form>")[0] + '</form>'
          localStorage.setItem('html', res)
          me.$router.push({
            path: '/user/alipay',
            query: {
              orderNum: me.params.orderSn
            }
          })
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally((res)=>{
        me.load = false
      })
    },
    init: function () {
      if (typeof(WebSocket) === "undefined") {
        alert("您的浏览器不支持socket")
      } else {
        // 实例化socket
        this.socket = new WebSocket(this.path)
        // 监听socket连接
        this.socket.onopen = this.open
        // 监听socket错误信息
        this.socket.onerror = this.error
        // 监听socket消息
        this.socket.onmessage = this.getMessage
      }
    },
    open: function () {
      console.log("socket连接成功")
    },
    error: function () {
      console.log("连接错误")
    },
    getMessage: function (msg) {
      console.log("1",msg.data)
      console.log("2",msg)
      if (msg.data.indexOf('orderSn') !== -1 && JSON.parse(msg.data).payStatus === 'success') {
        this.$router.push({
          path:'/user/paySuccess',
          query: {
            orderNum: JSON.parse(msg.data).orderSn
          }
        })
      }
    },
    send: function () {
      this.socket.send()
    },
    close: function () {
      console.log("socket已经关闭")
    }
  },
  destroyed () {
    this.flagTime = true
    // 销毁监听
    if (this.socket && this.socket.onclose) {
      this.socket.onclose = this.close()
    }
    window.removeEventListener('popstate', this.goBack, false);
  }
}
</script>

<style lang="stylus" scoped>
.paymentHead {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

  .content01, .content02, .content03 {
    width: 100%;
    height: 22px;
    text-align: center;
    line-height: 22px;
  }

  .content01 {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;

    img {
      width: 18px;
      height: 19px;
      margin-top -3px
    }

    span {
      margin-left 5px
      font-size: 15px;
      color: #999999;
    }
  }

  .content02 {
    margin-top: 15px;
    color: #222;
    font-weight: bold;

    span {
      font-size: 24px;
      font-weight: bold;
    }
  }

  .content03 {
    margin-top: 8px;
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #888888;
  }
}

.paymentTitle {
  margin: 25px 5.3% 0px;
  width: 89.3%;
  font-size: 15px;
  font-weight: bold;
  color: #000000;
}

.paymentMode {
  margin: 5px 5.3% 0px;
  width: 89.3%;
  height: 204px;

  .dgfk, .wechat, .alipay {
    width: 100%;
    height: 67px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #EFEFEF;

    .left {
      width: 121px;
      height: 22px;
      display: flex;
      justify-content: space-between;
      line-height: 22px;
      img {
        width: 22px;
        height: 22px;
      }
      span {
        width: 87px;
        height: 14px;
        font-size: 14px;
        font-weight: bold;
        color: #222222;
      }
    }

    .right {
      img {
        width: 22px;
        height: 22px;
      }
    }
  }
}
.paymentBtn {
  width: 88%;
  height: 48px;
  text-align center
  line-height 48px
  font-size: 16px;
  background: #FF4C4C;
  border: 1px solid #FF4C4C;
  border-radius: 4px;
  margin 50px 22px 0px 5.86%
  color #FFFFFF
  font-weight bold
}
>>>.van-button__text {
  font-size: 16px;
}
.wxtip {
  background: rgba(0, 0, 0, 0.8);
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  img {
    width: 100%;
  }
}
.wxtip-txt {
  margin-top: 107px;
  color: #fff;
  font-size: 20px;
  line-height: 1.8;
  .qx {
    height: 30px;
    background-color: #777474;
    padding: 2px 10px;
    border-radius: 4px;
  }
}
</style>


